export default {
	data() {
		return {
			phone:'',
			password:'',
			code:'',
			num:'获取验证码',
			invite:'',
		}
	},
	methods: {
		goService(){
			// uni.navigateTo({
			// 	url: '../service/service'
			// })
		},
		goLogin(){
			uni.navigateTo({
				url: '../login/login'
			})
		},
		register(){
			var phoneReg = /^[1]([3-9])[0-9]{9}$/;//电话
			if(!phoneReg.test(this.phone)){
				uni.showModal({
					content: '手机格式不正确',
					showCancel: false
				});
			    return false;
			}
			uni.request({
				url: this.url + '/mobile/register',
				method:'POST',
				header: {
					'content-type': 'application/x-www-form-urlencoded' ,// 默认值
				},
				data:{
					userCode:this.phone,
					veri:this.code,
					invitationNum:this.invite,
					password:this.password,
					phone:this.phone
				},
				success: (res) => {
					console.log(res)
					if(res.data.result){
						uni.showToast({
							title: '注册成功',
							duration: 2000,
						});
						uni.navigateTo({//注册成功后跳转到登录界面
							url: '../login/login'
						})
					}else{
						uni.showToast({
							title: res.data.msg,
							icon:'none',
							duration: 2000,
						});
					}
				},
				error:function(err){
				    uni.showToast({
				    	title: '注册失败，请稍后再试',
						icon:'none',
				    	duration: 2000,
				    });
				},
			})
		},
		sendCode(){
			let _this = this
			if( this.num != '获取验证码'){
			    return false;
			}
			if (!this.phone){
				uni.showModal({
					content: '手机号不能为空!',
					showCancel: false
				});
			    return false;
			}
			var phoneReg = /^[1]([3-9])[0-9]{9}$/;//电话
			if(!phoneReg.test(this.phone)){
			    uni.showModal({
			    	content: '手机号码格式不正确!',
			    	showCancel: false
			    });
			    return false;
			}
			uni.request({
				url: this.url + '/mobile/generateVeriCode',
				method:'POST',
				header: {
					'content-type': 'application/x-www-form-urlencoded' ,// 默认值
				},
				data:{
					type:'register',
					phone:this.phone
				},
				success: (res) => {
					console.log(res);
					if(res.data.result){
						uni.showToast({
							title: '发送成功',
							duration: 2000,
						});
						let num = 60
						let a = setInterval(function () {//设置定时器60s
						    num-=1
						    console.log(num)
						    _this.num = num
						    if(num<2){
						        clearInterval(a)
								_this.num = '获取验证码'
						    }
						},1000)
					}else{
						uni.showToast({
							title: '发送失败请稍后再试',
							icon:'none',
							duration: 2000,
						});
					}
				},
				error:function(xhr,textStatus){
				    uni.showToast({
				    	title: '发送失败请稍后再试',
				    	icon:'none',
				    	duration: 2000,
				    });
				},
			})
		},
	}
}
